<template>
  <div id="dashboard">
      <h3>Edit Employee</h3>
      <form @submit.prevent="updateEmployee">
            <div class="row">
              <div class="imput-field col s12">
                <input type="text" name="" id="" v-model="employee_id" disabled>
                <label>Employee ID#</label>
              </div>
            </div>
               <div class="row">
              <div class="imput-field col s12">
                <input type="text" name="" id="" v-model="name">
                <label>Name</label>
              </div>
            </div>
               <div class="row">
              <div class="imput-field col s12">
                <input type="text" name="" id="" v-model="dept">
                <label>Department</label>
              </div>
            </div>
               <div class="row">
              <div class="imput-field col s12">
                <input type="text" name="" id="" v-model="position">
                <label>Position</label>
              </div>
            </div>
               <div class="row">
              <button type="submit" class="btn">Submit</button>
              <router-link to="/" class="btn grey">Cancel</router-link>
            </div>
        </form>
  </div>
</template>
<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data: function () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.employee_id = doc.data().employee_id
          vm.name = doc.data().name
          vm.dept = doc.data().depart
          vm.position = doc.data().position
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.employee_id = doc.data().employee_id
          this.name = doc.data().name
          this.dept = doc.data().depart
          this.position = doc.data().position
        })
      })
    },
    updateEmployee () {
      db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            employee_id: this.employee_id,
            name: this.name,
            depart: this.dept,
            position: this.position
          }).then(() => {
            this.$router.push({name: 'view-employee',
              params: {employee_id: this.employee_id}})
          })
        })
      })
    }
  }
}
</script>



