<template>
  <div id="dashboard">
      <h3>dashboard</h3>

      <ul class="collection with-header">
        <li class="collection-header">
          <h4>Employees</h4>
        </li>
        <li v-for="employ in employee" :key="employ.id" class="collection-item"><div class="chip">{{employ.dept}}</div> {{employ.employee_id}}:{{employ.name}}
        <router-link class="secondary-content" :to="{name: 'view-employee', params: {employee_id: employ.employee_id}}">
<i class="fa fa-eye"></i>

        </router-link>
        </li>
      </ul>
      <div class="fixed-action-btn">
        <router-link to="/new" class="btn-flaoting btn-large red">
        <i class="fa fa-plus"></i>
        </router-link>
      </div>
  </div>
</template>
<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data: function () {
    return {
      employee: []
    }
  },
  created () {
    db.collection('employees').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'employee_id': doc.data().employee_id,
          'name': doc.data().name,
          'dept': doc.data().depart,
          'position': doc.data().position
        }
        this.employee.push(data)
      })
    })
  }
}
</script>



